import React, { Component } from 'react';
import Navigation from './Navigation';
import Facebook from "uikit/src/images/icons/facebook.svg";
import Twitter from "uikit/src/images/icons/twitter.svg";
import Instagram from "uikit/src/images/icons/instagram.svg";
import Whatsapp from "uikit/src/images/icons/whatsapp.svg";
class ContactUs extends Component {
    constructor(props) {
        super(props)
        this.state = {
            heading: "",
            body: "",
            email: ""
        }
    }

    submitHandler = (e) => {
        e.preventDefault()
        alert("Sorry this is sill in its development stage check back later. sorry for any inconveniencies")
        this.setState({
            heading: "", body: "", email: ""
        })
    }

    changeHandler = (e) => {
        const target = e.target
        const name = target.name
        this.setState({
            [name]: target.value
        })
    }
    render() {
        const { heading, body, email } = this.state
        return (
            <>
                <Navigation />
                <div>
                    <h3 style={{ color: "rgb(206, 60, 41)" }} className="uk-heading-line uk-text-center"><span>Send us a mail</span></h3>

                    <form onSubmit={(e) => this.submitHandler(e)}>
                        <center>
                            <input className="uk-input uk-margin uk-width-5-6 uk-width-1-2@m" type="text" name="heading" id="head" placeholder="Heading for your mail" value={heading} onChange={e => this.changeHandler(e)} /> <br />
                            <textarea style={{ height: "15vh" }} className="uk-textarea  uk-width-5-6 uk-width-1-2@m" name="body" id="message" placeholder="body of mail" value={body} onChange={e => this.changeHandler(e)}></textarea><br />
                            <input className="uk-input uk-margin  uk-width-5-6 uk-width-1-2@m" type="email" name="email" id="email" placeholder="Enter your email address" value={email} onChange={e => this.changeHandler(e)} /> <br />

                            <input type="submit" value="Send" className="uk-button uk-flex uk-flex-center" />
                        </center>
                    </form>


                    <h3 style={{ color: "rgb(206, 60, 41)" }} className="uk-heading-line uk-text-center"><span>Follow us on social media</span></h3>
                    <div className="uk-flex uk-flex-center">
                        <img src={Facebook} alt="faceboot" className="uk-icon-button uk-button-default" />
                        <img src={Twitter} alt="faceboot" className="uk-icon-button uk-button-default uk-margin-small-left" />
                        <img src={Instagram} alt="faceboot" className="uk-icon-button uk-button-default uk-margin-small-left" />
                        <img src={Whatsapp} alt="faceboot" className="uk-icon-button uk-button-default uk-margin-small-left" />
                    </div>

                </div >
            </>
        );
    }
}

export default ContactUs;