import React, { Component } from 'react';
import Boy from "./boy.jpg";
import Avatar from "./avatar.jpg";
import Success from "./success.jpg";
import "./gallery.css";
import Navigation from "./Navigation.jsx"

class Gallery extends Component {
    constructor(props) {
        super(props);
        this.state = {}
    }

    render() {
        return (
            <>
                <Navigation />
                <div className="row">
                    <div className="column">
                        <img src={Boy} />
                        <img src={Avatar} />
                        <img src={Success} />
                        <img src={Boy} />
                        <img src={Avatar} />
                        <img src={Success} />
                        <img src={Boy} />
                    </div>
                    <div className="column" >
                        <img src={Avatar} />
                        <img src={Success} />
                        <img src={Boy} />
                        <img src={Boy} />
                        <img src={Avatar} />
                        <img src={Avatar} />
                    </div>
                    <div className="column">
                        <img src={Boy} />
                        <img src={Avatar} />
                        <img src={Success} />
                        <img src={Boy} />
                        <img src={Avatar} />
                        <img src={Success} />
                        <img src={Boy} />
                    </div>
                </div >

            </>
        );
    }
}

export default Gallery;