import React from 'react';
import "uikit/dist/css/uikit.min.css";
import "uikit/dist/js/uikit.min.js";
import "uikit/dist/js/uikit-icons.js";
import { Link } from "react-router-dom";
import { QuestionsContext } from "../context/QuestionsContext.jsx"
import "./questions.css";

class Questions extends React.Component {
    static contextType = QuestionsContext;
    render() {
        return (
            <div style={{ minHeight: "100vh" }}>
                <Form />
            </div>
        );
    }

}

class Form extends React.Component {
    static contextType = QuestionsContext;
    render() {
        const { questions, active, changeHandler, correctCounter, activeHandler } = this.context;
        const { question, option1, option2, option3, option4, option1RawInput, option2RawInput, option3RawInput, option4RawInput, id, option1Checked, option2Checked, option3Checked, option4Checked } = active;
        return (
            <div>
                <h1 className="uk-flex uk-flex-center uk-padding-small">Question {id + 1}</h1>

                <form className="uk-margin-small-left uk-margin-small-right" >

                    <h4>{question}</h4>
                    <input className="inp uk-margin-small" type="radio" name="options" id={option1} value={option1RawInput} onChange={(e) => changeHandler(e, id)} checked={option1Checked} />{option1}<br />
                    <input className="inp uk-margin-small" type="radio" name="options" id={option2} value={option2RawInput} onChange={(e) => changeHandler(e, id)} checked={option2Checked} />{option2}<br />
                    <input className="inp uk-margin-small" type="radio" name="options" id={option3} value={option3RawInput} onChange={(e) => changeHandler(e, id)} checked={option3Checked} />{option3}<br />
                    <input className="inp uk-margin-small" type="radio" name="options" id={option4} value={option4RawInput} onChange={(e) => changeHandler(e, id)} checked={option4Checked} />{option4}<br />

                    <div className="uk-flex-center uk-position-bottom uk-margin-medium-bottom">

                        <div className=" uk-child-width-1-5 uk-margin-bottom uk-margin-left uk-margin-right" >
                            {questions.map(question => (
                                <Buttons quest={question} key={Math.random()} onClick={() => activeHandler(question)} color={question.color} />
                            ))}
                        </div>
                        <center>
                            <Link to="/result">
                                < input type="submit" value="Submit" className="submit-btn uk-button uk-button-default uk-text-primary uk-width-1-1 uk-width-1-6@m uk-margin-small-bottom uk-margin-small-top " onClick={correctCounter} />
                            </Link>
                            <Link to="/result">
                                <button className="quit-btn uk-button uk-button-danger uk-width-1-1 uk-width-1-6@m uk-margin-small-bottom uk-margin-small-top ">Quit</button>
                            </Link>
                        </center>
                    </div>

                </form >
            </div>
        );
    }
}

function Buttons(props) {
    //const { color } = props.color

    return (
        <button className="nav-btn uk-button uk-button-default" onClick={props.onClick} style={{ backgroundColor: `${props.color}` }}>
            {props.quest.id + 1}
        </button>
    )
}
export default Questions;